.main{
  
  width: 100%;
  position: relative;
}
.main img{
  height: 400px;
  width: 100%;
}
.content{
  position:absolute;
  top: 80px;
  left: 100px;
  max-width: 600px;
  color: white;
}

 .subspecial{
  padding-bottom: 50px;
 }
 
.subspecial h2{
  color: #000;
  margin-bottom: 18px;
  font-size: 30px;
  text-align: center;
  margin-top: 10px;
}
.subspecial p{
  line-height: 18px;
  text-align: center;
  position: absolute;
  right: 35%;
  max-width: 400px;
  margin-top: 2px;

  
}
.flex{
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
  margin-top: 90px;
  align-items: center;
}
        
.flex img{
  width: 200px;
  margin: auto;
  margin-bottom: 40px;
  margin-right: 80px;
  border-radius: 15px;
  
}
.flex li{
  list-style: none;
}.flex h4{
  margin-top: 2px;
  padding-bottom: 30px;
}
.catbtn{
  display: flex;
  flex-wrap: wrap;
  margin-right: 130x;
  background-color: black;
  padding-bottom: 30px;
  width: 100%;
}
.catbtn button{
  color:aliceblue;
  font-weight: bolder;
  background-color: orange;
  border-radius: 10px;
  width: 100px;
  height: 40px;
  margin-right: 1px;
  margin-top: 40px;
  margin-left: 60px;
  cursor: pointer;
  
}.catbtn button:hover{
  background-color: #7c7468;
  color: black;
}
.flex h3{
  color: red;
  text-align: center;
  margin-left: 43%;
}